import {
    Tabs,
    TabsHeader,
    TabsBody,
    Tab,
    TabPanel,
  } from "@material-tailwind/react";
  import React from "react";

  export function HobbyTabs() {
    const data = [
        {
            label: "Digital Art",
            value: "art",
            desc: "Here's art stuff"
        },
        {
            label: "Piano",
            value: "piano",
            desc: "Here's piano"
        },
        {
            label: "Photography",
            value: "photo",
            desc: "Some photos"
        },
        {
            label: "Violin",
            value: "violin",
            desc: "All Violin"
        }
    ];
    return (
        <Tabs value="html" orientation="vertical">
            <TabsHeader>
                {data.map(({label, value}) => (
                    <Tab key={value} value={value}>
                        {label}
                    </Tab>
                ))}
            </TabsHeader>
            <TabsBody>
                {data.map(({value, desc}) => {
                    <TabPanel key={value} value={value}>
                        {desc}
                    </TabPanel>
                })}
            </TabsBody>
        </Tabs>
     )
  }